import React from 'react';
import locationImg from "../imgs/location.png"
import { ReactComponent as LocIcon } from '../imgs/064-pin.svg';
import { ReactComponent as PhoneIcon } from '../imgs/086-telephone.svg';
import { Colors } from './Colors';
import logoImg from "../imgs/logo.png"
const Footer = () => {

    const FooterFlexBox = {
        width: "100%",
        height: "30vh",
        backgroundColor: Colors.projectDark5,
        display: "flex",
        flexDirection: "row",
        

    }
    const FooterFlexBoxItemsimg = {
        width: "30%",
        
        justifyContent: "flex-end",
        flexGrow: 3, 
        padding: "30px"

    }
    const FooterFlexBoxItemsText = {
        width: "10%",
        flexGrow: 1,
        justifyContent: "flex-end",
        textAlign: "right",
        paddingTop: "30px",
       
    }
    const FooterFlexBoxItemsTextEnd = {
        width: "10%",
        flexGrow: 1,
        justifyContent: "flex-end",
        textAlign: "right",
        paddingTop: "30px",
        marginRight: "3vw"
    }

    const logoImgCss ={
        width: "20%",
        maxWidth: "100px"
         
    }
    const TextTitle = {
        fontSize: "1.4rem",
        fontWeight: "600",
        color: Colors.whiteColor,
        marginBottom: "20px"
    }
    const TextContent = {
        fontSize: "1.0rem",
        fontWeight: "400",
        marginBottom: "5px",
        color: Colors.whiteColor,
    }
    return (
        <div style={FooterFlexBox}>
            <div style={FooterFlexBoxItemsimg}>

                 <img src={logoImg} style={logoImgCss} />

            </div>
            
            <div style={FooterFlexBoxItemsTextEnd}>
                 <div style={TextTitle}>Products   </div>
                 <div style={TextContent}>Nutty Data   </div>
                 <div style={TextContent}>X-Link   </div>
                 <div style={TextContent}> Projects1  </div>
                 <div style={TextContent}> Projects2 </div>
                 <div style={TextContent}>Projects3  </div>

            </div>
        </div>
    )
}

export default Footer
