import React from 'react';


import { useState } from 'react';
import { ReactComponent as AlgoIcon } from '../imgs/icons/003-algorithm.svg';
import { ReactComponent as BinIC } from '../imgs/icons/005-binary code.svg';
import { ReactComponent as CodeIC } from '../imgs/icons/006-coding.svg';
import { ReactComponent as AppDevIC } from '../imgs/icons/010-app development.svg';
import { ReactComponent as BigDataIC } from '../imgs/icons/011-big data.svg';
import { ReactComponent as HardWareIC } from '../imgs/icons/motherboard.svg';


import testImg from "../imgs/layer1.png"
const ProjectsItems = ({ bgc, textCol , category, title, content, imgSrc }) => {

    const [transitionPx , setTransitionPx ] = useState(0); 
    const ProjectsItemsContainer = {
        width: "100%",
        height: "100%",
        maxWidth: "800px",
        display: "flex",
        flexDirection: "row",
        flexWrap: "wrap",
        justifyContent: "center",
        alignItems: "center",
        margin: "auto",
        backgroundColor: `${bgc}`,
        color :`${textCol}`,
        //   border: "2px solid #FFF",
        border:  "3px solid " + `${textCol}` , 
        borderRadius: "25px",
        overFlow: "hidden",
        transition: "0.3s",
        
    };
    const ProjectsItemsContents = {
        display: "flex",
        flexDirection: "column",
        width: "35%",
        padding: "10px",
        
        height: "90%"
    }
    const ProjectsItemsContentsCategory = {
        flexGrow: "1",
        fontSize: "1.0rem",
        display: "flex",
        flexDirection: "row",
    };
    
    const ProjectsItemsContentsTitle = { 
        fontWeight: "600",
        fontSize: "1.3rem",
        
    };
    const ProjectsItemsContentsText = {
        fontWeight: "400",
        fontSize: "0.9rem",
        marginTop: "auto"
    };
    const ProjectsItemsImgContents = {
        width: "55%",
        height: "95%",
        padding: "auto"
        
    };
    const ProjectsItemsImg = {
        height: "100%",
        width: "100%",
        borderRadius: "50px",
        verticalAlign: "middle",
        margin: "auto",
        objectFit: "contain",
        
        
    };
    function categorySelector(category){
        if(category ==1) {
            return [
                <div style={ProjectsItemsContentsCategory}> 
                <AlgoIcon style={{ width: "1.5rem", height:"1.5rem", marginRight: "15px", }} />
             Algorithm </div> ];
        }else if (category ==2){
            return [
                <div style={ProjectsItemsContentsCategory}> 
                <BinIC style={{ width: "1.5rem", height:"1.5rem", marginRight: "15px", }} />
             Firmware </div> ];
        }
        else if (category ==3){
            return [
                <div style={ProjectsItemsContentsCategory}> 
                <CodeIC style={{ width: "1.5rem", height:"1.5rem", marginRight: "15px", }} />
             Coding </div> ];
        }
        else if (category ==4){
            return [
                <div style={ProjectsItemsContentsCategory}> 
                <AppDevIC style={{ width: "1.5rem", height:"1.5rem", marginRight: "15px", }} />
             App Development </div> ];
        }
        else if (category ==5){
            return [
                <div style={ProjectsItemsContentsCategory}> 
                <BigDataIC style={{ width: "1.5rem", height:"1.5rem", marginRight: "15px", }} />
             Big data / Backend </div> ];
        }
        else if (category ==6){
            return [
                <div style={ProjectsItemsContentsCategory}> 
                <HardWareIC style={{ width: "1.5rem", height:"1.5rem", marginRight: "15px", }} />
             Hardware </div> ];
        }
         


    }
    return (
        <div style={ProjectsItemsContainer} >
            <div style={ProjectsItemsContents}>
                {categorySelector(category)}
               
                <div style={ProjectsItemsContentsText}> 
               
                <div style={ProjectsItemsContentsTitle}> {title}  </div>
                <br/>
                {content}

                </div>
            </div>

            <div style={ProjectsItemsImgContents}>
                <img style={ProjectsItemsImg} src={imgSrc} />
            </div>

        </div>
    )
}

export default ProjectsItems