import React from 'react';
import Slider from 'react-slick';
// needs npm install slick-carousel 
import "slick-carousel/slick/slick.css";
//import "slick-carousel/slick/slick-theme.css";
import "../Components/ComponentsCss/slick.css";
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/all';
import { Parallax } from 'react-scroll-parallax';
import { useEffect, useState, useRef } from 'react';
import { Colors } from './Colors';

const SlideShow = () => {
  const SlideShowContainer = {
    width: "100vw",
    margin: "auto",
    transform: "rotate(5deg)"
  }
  const SlideShowItems = {
    width: "150px",
    margin: "auto",
    fontSize: "3.5rem",
    fontWeight: "bold",
    color: Colors.accentColor  ,
    border: " 2px solid #000 !important",
    borderRadius: " 5px",

  }
  const settings = {

    infinite: true,
    autoplay: true,
    autoplaySpeed: 0,
    speed: 3000,
    arrows: false,
    variableWidth: true,
    cssEase: "linear",
    dots: false,
    pauseOnHover: false
  };

     /*
        gsap.registerPlugin(ScrollTrigger);
        //ScrollTrigger.refresh();
        //ScrollTrigger.normalizeScroll(true);
        ScrollTrigger.config({ 
            ignoreMobileResize: true
          });
          */
  const el = useRef();
  const q = gsap.utils.selector(el);
  const tl = useRef();  // store the timeline in ref 
  useEffect(() => {
    // add box and circle anim to our timeline and play the first render 
    ScrollTrigger.config({ 
      ignoreMobileResize: true
    });
    //ScrollTrigger.normalizeScroll(true);
    tl.current && tl.current.progress(0).kill();

    tl.current = gsap.timeline({
      scrollTrigger: {
        trigger: "#slideshowcont",
        start: "top bottom",
        end: "bottom center",

        scrub: 1,
        // pin: true causing pinning problem (moves after end ) 
      }
    })
      .to(q("#slideshowcont"), {
        transform: "rotate(0deg)",

      })
      ;

  }, []);

  return (
    <div ref={el}>

      
      <div id="slideshowcont" style={SlideShowContainer}>
        <Parallax speed={5}>
          <Slider id="slider" {...settings}>


            <div style={SlideShowItems} className={"textDeco"}>
              Circuit Design
            </div>
            <div style={SlideShowItems} className={"textDeco"}>
              Swift UI
            </div>
            <div style={SlideShowItems} className={"textDeco"}>
              Android
            </div>
            <div style={SlideShowItems} className={"textDeco"}>
              Ev charger
            </div>
            <div style={SlideShowItems} className={"textDeco"}>
              HardWare
            </div>
            <div style={SlideShowItems} className={"textDeco"}>
              IoT
            </div>
            <div style={SlideShowItems} className={"textDeco"}>
              Embedded
            </div>

            <div style={SlideShowItems} className={"textDeco"}>
              Circuit Design
            </div>
            <div style={SlideShowItems} className={"textDeco"}>
              Swift UI
            </div>
            <div style={SlideShowItems} className={"textDeco"}>
              Android
            </div>
            <div style={SlideShowItems} className={"textDeco"}>
              Ev charger
            </div>
            <div style={SlideShowItems} className={"textDeco"}>
              HardWare
            </div>
            <div style={SlideShowItems} className={"textDeco"}>
              IoT
            </div>
            <div style={SlideShowItems} className={"textDeco"}>
              Embedded
            </div>
          </Slider>
        </Parallax>
      </div>
    </div>
  )
}

export default SlideShow

