
import './App.css';
import Spacer from './Components/Spacer';
import LogoScroll from './Components/LogoScroll.js';
import LogoScrollGSAP from './Components/LogoScrollGSAP';
import HorizontalScrollItems from './Components/HorizontalScrollComponents/HorizontalScrollItems';
import VideoScroll from './Components/VideoScroll';
import ConstructScroll from './Components/ConstructScroll';
import Basic from './Components/Basic';
import ComparisonScroll from './Components/ComparisonScroll';
import DeconstructScroll from './Components/DeconstructScroll';
import ParrellexScroll from './Components/ParrellexScroll';
import TopBarMenu from './Components/TopBarMenu';
import SideMenuBar from './Components/SideMenuBar';
import MainPage from './Components/MainPage';
import SlideShow from './Components/SlideShow';
import ProjectsContainer from './Components/ProjectsContainer';
import CompanySlideShow from './Components/CompanySlideShow';
import BGColorChange from './Components/BGColorChange';
import HorizontalScrollGsap from './Components/HorizontalScrollGsap';
import ContactUs from './Components/ContactUs';
import Footer from './Components/Footer';
// images and videos 
import compLogoImg from './imgs/logo.png';
import mainvid from './videos/mainvideo.gif';
import mainvid2 from './videos/mainvideo2.gif';
import cookingvid from './videos/cooking.mp4';
import spinvid from './videos/spin.mp4';
import discovid from './videos/disco.mp4';
import menuImg from './imgs/menuImg.png';

import { Colors } from './Components/Colors';

import { useEffect, useCallback, useState } from 'react';
// parrallax 
import { ParallaxProvider } from 'react-scroll-parallax';
import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';

function App() {
  function getWindowDimensions() {
    const { innerWidth: width, innerHeight: height } = window;
    return {
      width,
      height
    };
  }

  const [windowDimensions, setWindowDimensions] = useState(getWindowDimensions());

  useEffect(() => {

    gsap.registerPlugin(ScrollTrigger);
    //S
    //Scrollbar.init(el);
       /*
        
        //ScrollTrigger.refresh();
        //ScrollTrigger.normalizeScroll(true);
        ScrollTrigger.config({ 
            ignoreMobileResize: true
          });
          */


    //bodyScrollBar.addListener(ScrollTrigger.update);

    function handleResize() {
      setWindowDimensions(getWindowDimensions());
    }
    //window.history.scrollRestoration = 'manual';
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const mainPageMobileContainer = {
    width: "100vw",
    height: "100vh",
    

  }
  const mainPageContainer = {
    width: "90vw",
    height: "100vh",
    
  }
  const mainPageContainerRed = {
    width: `${windowDimensions.width - 80}` + "px",
    overflow: "hidden",
  }
  return (
    <>


      <ParallaxProvider> .
        <SideMenuBar />
        <div style={windowDimensions.width < 800 ? mainPageMobileContainer : windowDimensions.width > 800 ? mainPageContainerRed : mainPageContainer}>
          <LogoScrollGSAP imgsrc={compLogoImg} />
          <div id="MainPageView"></div>
          <Spacer marginTopVh={10} />

          <MainPage />
          <Spacer marginTopVh={15} />
          <SlideShow />
          <div id="ProjectPageView"></div>
          <Spacer marginTopVh={10} />

          <ProjectsContainer />
          <Spacer marginTopVh={10} />
          <CompanySlideShow />
          <div id="BgColorChangePageView"></div>
          < BGColorChange scrHeight={60} colorTo={"333533"} speId={"startColorCh"} />
          <div id="whyJCPageView"></div>
        
          <HorizontalScrollGsap />
          <div id="ContactUsPageView"></div>
          < BGColorChange scrHeight={30} colorTo={"F0F4EF"} speId={"endColorCh"} />
          <ContactUs />
          <Spacer marginTopVh={30} />
          <Footer />
        </div>
      </ParallaxProvider>




    </>
  );
}

