import React from 'react';
import ProjectsItems from './ProjectsItems';
import { useEffect } from 'react';
import useWindowDimensions from './getWindowsDimensions.js';
import { Colors } from './Colors';
// project img sources 
import projectImg1 from "../imgs/layer2.png";
import tunnelImg from "../imgs/ProjectImgs/tunnel.png";
import elecImg from "../imgs/ProjectImgs/electriccar.png";
import droneImg from "../imgs/ProjectImgs/drone.png";
import rfImg from "../imgs/ProjectImgs/rfsensor.png";
const ProjectsContainer = () => {
    const { height, width } = useWindowDimensions();
    const widthColPx = 1200;

    const projectsContainer = {
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        flexWrap: "wrap",
        height: "auto",

        width: "100%",
        maxWidth: "1600px",
        margin: "auto",
    };

    const projectsItemsSingleColbgc = {
        width: "90%",

        margin: "10px",
        borderRadius: "25px",

        height: "40vh",
    }
    const projectsItemsMulColbgc = {
        width: "45%",

        margin: "10px",
        borderRadius: "25px",

        height: "40vh",
        padding: "10px",
    }

    /* 
        if(category ==1) {
             
                <AlgoIcon style={{ width: "2rem", height:"2rem", marginRight: "15px", }} />
             Algorithm </div> ];
        }else if (category ==2){
           
             Firmware </div> ];
        }
        else if (category ==3){
           
             Coding </div> ];
        }
        else if (category =4){
           
             App Development </div> ];
        }
        else if (category ==5){
           
             Big data / Backend </div> ];
        }

    */
    return (
        <div style={projectsContainer}>
            <div style={width > widthColPx ? projectsItemsMulColbgc : projectsItemsSingleColbgc}>
                <ProjectsItems bgc={Colors.projectDark5} textCol={Colors.projectLight3} title="터널 재난방송시스템" content="국도의 터널에 설치되는 AM, FM, DMB 송수신 장치 및 AM, FM 비상방송 제어 모듈을 개발. "
                    imgSrc={tunnelImg} category="2" 
                />
            </div>
            <div style={width > widthColPx ? projectsItemsMulColbgc : projectsItemsSingleColbgc}>
                <ProjectsItems bgc={Colors.projectLight3} textCol={Colors.projectDark5} title="전기자동차 충전 패널" content="전기자동차 완속 충전 패널 및 통신 장비, CP(Charger Point) 장비 "
                    imgSrc={elecImg} category="6" 
                />
            </div>
            <div style={width > widthColPx ? projectsItemsMulColbgc : projectsItemsSingleColbgc}>
                <ProjectsItems bgc={Colors.projectDark5} textCol={Colors.mainColor} title=" 무인기(드론) RF 송수신반 제어기" content="무인기에 탑재되어 RF 통신의 송수신 주파수를 제어하는 주제어 장치(CPU) 모듈"
                    imgSrc={droneImg} category="6"
                />
            </div>
            <div style={width > widthColPx ? projectsItemsMulColbgc : projectsItemsSingleColbgc}>
                <ProjectsItems bgc={Colors.projectLight3} textCol={Colors.projectDark5} title="RF Plasma Chamber  감시장치 및 센서" content="감시장치 센서 제품 생산. 센서 데이터 분석 웹 서비스 플랫폼 (NuttyData)"
                    imgSrc={rfImg} category="6"
                />
            </div>
             

        </div>
    )
}

export default ProjectsContainer