import React from 'react';
import { Colors } from './Colors';
import { useEffect, useState, useRef } from 'react';
// imgs 
import contactUsImg from "../imgs/contactus/contactus.png";
// window size cal 
import useWindowDimensions from './getWindowsDimensions.js';

const ContactUs = () => {

    const { height, width } = useWindowDimensions();


    const ContactUsContainer = {
        width: "100%",
        height: "100%",
        marginBottom: "200px"
    }
    const ContactUsTitle = {
        fontSize: "8rem",
        textAlign: "center",
        wordWrap: "break-word",
        color: Colors.whiteColor
    }
    const ContactUsMainCompo = {
        width: "90%",
        maxWidth: "1200px",

        height: "50vh",
        backgroundColor: Colors.projectDark5,
        borderRadius: "50vh",
        margin: "auto",
        display: "flex",
        justifyContent: "center"

    }
    const ContactUsMainCompoMobile = {
        width: "90%",
        maxWidth: "1200px",

        height: "90vh",
        backgroundColor: Colors.darkColor,
        borderRadius: "50vh",
        margin: "auto",
        display: "flex",
        justifyContent: "center"

    }
    const ContactUsMainCompoFlexBox = {
        width: "60%",
        height: "80%",
        display: "flex",
        flexDirection: "row",
        margin: "auto",
        justifyContent: "center",
        alignItems: "center",
    }
    const ContactUsMainCompoFlexBoxMobile = {
        width: "70%",
        height: "100%",
        display: "flex",
        flexDirection: "column",
        margin: "auto",
        justifyContent: "center",
        alignItems: "center",
    }
    const ContactUsMainCompoDetails = {
        width: "60%",
    }
    const ContactUsMainCompoDetailsMobile = {
        width: "90%",
    }
    const ContactUsMainCompoDetailsTitle = {
        fontSize: "2.5rem",
        fontWeight: "bold",
        color: Colors.projectLight3,
    }
    const ContactUsMainCompoDetailsText = {
        fontSize: "1.2rem",
        color: Colors.projectLight3,
    }
    const ContactUsMainCompoDetailsFlexBox = {
        height: "30%",
        display: "flex",
        flexDirection: "row",
        justifyContent: "center",
        alignItems: "center",
        marginTop: "20px"

    }
    const ContactUsMainCompoDetailsFlexBoxItems = {
        fontSize: "0.8rem",
        width: "50%",
        textAlign: "center",
        color: Colors.projectLight3,
    }
    const ContactUsMainCompoDetailsImgCont = {
        width: "40%",
    }
    const ContactUsMainCompoDetailsImgContMobile = {
        width: "100%",
    }
    const ContactUsMainCompoDetailsImg = {
        margin: "auto",
        height: "100%",
        width: "100%",
        marginLeft: "10vw",
        scale: "2",
        objectFit: "contain",
    }
    const ContactUsMainCompoDetailsImgMobile = {
        margin: "auto",
        height: "100%",
        width: "100%",
         
        scale: "1.5",
        objectFit: "contain",
    }

    function ContactUsCheckWidth({ width }) {
        if (width > 800) {
            return (
                <>
                    <div style={ContactUsContainer}>
                        <div style={ContactUsTitle}> LET'S TALK  </div>
                    </div>
                    <div style={ContactUsMainCompo}>
                        <div style={ContactUsMainCompoFlexBox}>
                            <div style={ContactUsMainCompoDetails}>
                                <div style={ContactUsMainCompoDetailsTitle}>
                                    Any Questions?

                                </div>
                                <br />
                                <div style={ContactUsMainCompoDetailsText}>
                                    Answers are just an email away.

                                </div>
                                <div style={ContactUsMainCompoDetailsText}>
                                    <br />
                                    Drop us a line and the right person will get back to you as soon as possible
                                </div>

                                <div style={ContactUsMainCompoDetailsFlexBox}>
                                <div style={ContactUsMainCompoDetailsFlexBoxItems}>jcom@gmail.com </div>
                                <div style={ContactUsMainCompoDetailsFlexBoxItems}>010-2525-5115</div>
                                </div>
                            </div>


                            <div style={ContactUsMainCompoDetailsImgCont}>
                                <img style={ContactUsMainCompoDetailsImg} src={contactUsImg} />
                            </div>
                        </div>

                    </div>
                </>
            )
        } else {
            return (<>
                <div style={ContactUsContainer}>
                    <div style={ContactUsTitle}> LET'S TALK  </div>
                </div>
                <div style={ContactUsMainCompoMobile}>
                    <div style={ContactUsMainCompoFlexBoxMobile}>
                        <div style={ContactUsMainCompoDetailsTitle}>
                            Any Questions?

                        </div>
                        <div style={ContactUsMainCompoDetailsMobile}>

                            <div style={ContactUsMainCompoDetailsImgContMobile}>
                                <img style={ContactUsMainCompoDetailsImgMobile} src={contactUsImg} />
                            </div>
                            <br />
                            <div style={ContactUsMainCompoDetailsText}>
                                Answers are just an email away.

                            </div>
                            <div style={ContactUsMainCompoDetailsText}>
                                <br />
                                Drop us a line and the right person will get back to you as soon as possible
                            </div>

                            <div style={ContactUsMainCompoDetailsFlexBox}>
                                <div style={ContactUsMainCompoDetailsFlexBoxItems}>jcom@gmail.com </div>
                                <div style={ContactUsMainCompoDetailsFlexBoxItems}>010-2525-5115</div>
                            </div>
                        </div>



                    </div>

                </div>
            </>);
        }
    }

    return (
        <>
            <ContactUsCheckWidth width={width} />
        </>
    )
}

export default ContactUs