import React from 'react';
import { useEffect, useState, useRef } from 'react';
import gsap from 'gsap';

const box_des = {
    width: "150px", 
    height: "150px",
    backgroundColor: "green"
}

function Box({children}) {
    return <div  style ={box_des} id = 'box'   > {children}</div>;
}
function Circle({children}){
    return <div className='circle' id = 'circle'> {children} </div>
}
const Basic = () => {
    const el = useRef(); 
    const q = gsap.utils.selector(el);
    const tl = useRef();  // store the timeline in ref 
    const [reversed, setReversed] = useState(false); 

    useEffect(() =>{
        // add box and circle anim to our timeline and play the first render 
        tl.current && tl.current.progress(0).kill(); 
        
        tl.current = gsap.timeline()
            .to(q("#box"),{
                rotate: 360
            })
            .to(q("#circle"), {
                x : 100 
            });
    }, []); 
     
    useEffect(() => {
        // toggle the direction of our timeline
        console.log("toggling reverse to", reversed);
        tl.current.reversed(reversed);    
    }, [reversed]);
    return (

        <div ref={el}>
            <div>
                <button onClick={() => setReversed(!reversed)}>
                    Toggle
                </button>
            </div>
            <Box> Box</Box>
            <Circle> Circle</Circle>
        </div>
    )
}

export default Basic