import React from 'react';
import { useEffect, useState, useRef } from 'react';
import gsap from 'gsap';
// imgs 
import menuImg from "../imgs/menuImg.png";
import homeImg from "../imgs/home.png";
import projectsImg from "../imgs/projects.png";
import companiesImg from "../imgs/companies.png";
import whyJCImg from "../imgs/star.png";

// window size cal 
import useWindowDimensions from './getWindowsDimensions.js';

// link 
import { Link } from "react-scroll";

const SideMenuBar = () => {

    const [menuHover, setMenuHover] = useState(false);

    const { height, width } = useWindowDimensions();
    const widthColPx = 800;

    const [shadowPx, SetShadowPx] = useState(0);
    const [menuImgSz, setMenuImgSz] = useState(40);
    const el = useRef();
    const q = gsap.utils.selector(el);
    const el2 = useRef();
    const q2 = gsap.utils.selector(el2);
    const [tl, setTl] = useState();
    const [menuOpen, setMenuOpen] = useState(false);


    const sideMenuContainer = {
        position: "fixed",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        height: "98vh",
        width: "10vw",
        maxWidth: "80px",
        minWidth: "50px",
        bottom: "1vh",
        top: "1vh",
        right: "0vw",

        zIndex: "10",
        overflow: "visible"
    };
    const sideMenuContainerMobile = {
        position: "fixed",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        height: "50px",
        width: "50px",


        top: "1vh",
        right: "2vw",

        zIndex: "10",
        overflow: "visible"
    };


    const sideMenuCSS = {
        height: "100%",
        width: "100%",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        backgroundColor: "#FFF",
        borderRadius: "10px",

        margin: "auto",

        boxShadow: "inset 0 0 " + `${shadowPx}` + "px #6d6d6d",
        transition: "0.1s",
        zIndex: "10",

        // `url(${loc1})`
    };
    const sideMenuContentsCont = {
        position: "fixed",
        height: "96vh",
        width: "30vw",

        right: "-30vw",
        backgroundColor: "#bcbabe",
        zIndex: "9",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        borderRadius: "15px",

    }


    const sideMenuContentsContMobile = {
        position: "fixed",
        height: "95vh",
        width: "90vw",
        top: "-95vh",
        left: "5vw",
        right: "5vw",
        backgroundColor: "#bcbabe",
        zIndex: "9",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        borderRadius: "15px",

    }

    const sideMenuContents = {
        padding: "10px",
        height: "95%",
        width: "85%",
        borderRadius: "15px",
        boxShadow: "15px 15px 20px 10px rgba(66,66,66, 0.8) inset, -10px 0px 20px 0px #dedede ",

    }
    const imgCss = {
        maxWidth: `${menuImgSz}` + "px",
        transition: "0.1s",
    }
    const menuItemsContainer = {
        display: "flex",
        flexDirection: "row",
        height: "10vh",
        width: "100%",
        justifyContent: "start",
        alignItems: "center",
        marginTop: "20px"

    }
    const menuItemsImgCss = {
        width: "auto",
        height: "50%"
    }
    const menuItemsText = {
        display: "flex",
        alignItems: "center",
        fontSize: "1.2rem",
        height: "100%",
        marginLeft: "20px",

    }
    function onMenuHover() {
        SetShadowPx(15);
        setMenuImgSz(35);
        setMenuHover(true);
    }
    function onMenuLeave() {
        SetShadowPx(0);
        setMenuImgSz(40);
        setMenuHover(false);
    }
    function onMenuClicked() {

        setMenuOpen(!menuOpen);
    }

    function closeModal() {
        setMenuOpen(!menuOpen);
    }

    useEffect(() => {
        const tl = gsap.timeline();
        setTl(tl);
    }, []);


    useEffect(() => {
        console.log("menu open : ", menuOpen);
        if (width < widthColPx) {
            // mobile mode 
            if (menuOpen == true) {
                tl && tl
                    .add('open')
                    .to(q("#sideMenuCss"), {
                        duration: 0.2,
                        x: "-40vw",
                        height: "50px",
                        width: "50px",
                        borderRadius: "50px",
                    }, 'open')
                    .to(q("#sideMenuImg"), {
                        duration: 0.1,
                        rotate: 90,
                    }, 'open')
                    .to(q2("#sideMenuContentsCont"), {
                        duration: 0.3,
                        y: "100vh",
                    }, 'open');

            } else {
                tl && tl
                    .add('close')
                    .to(q("#sideMenuCss"), {
                        duration: 0.2,
                        x: "0vw",


                        borderRadius: "10px",
                    }, 'close')
                    .to(q("#sideMenuImg"), {
                        duration: 0.1,
                        rotate: 0,
                    }, 'close')
                    .to(q2("#sideMenuContentsCont"), {
                        duration: 0.6,
                        y: "-100vh",
                    }, 'close');
            }
        } else {
            if (menuOpen == true) {
                tl && tl
                    .add('open')
                    .to(q("#sideMenuCss"), {
                        duration: 0.2,
                        x: "-27vw",
                        height: "50px",
                        width: "50px",
                        borderRadius: "50px",
                    }, 'open')
                    .to(q("#sideMenuImg"), {
                        duration: 0.1,
                        rotate: 90,
                    }, 'open')
                    .to(q2("#sideMenuContentsCont"), {
                        duration: 0.3,
                        x: "-30vw",
                    }, 'open');

            } else {
                tl && tl
                    .add('close')
                    .to(q("#sideMenuCss"), {
                        duration: 0.2,
                        x: "0vw",
                        height: "98vh",
                        width: "10vw",
                        borderRadius: "10px",
                    }, 'close')
                    .to(q("#sideMenuImg"), {
                        duration: 0.1,
                        rotate: 0,
                    }, 'close')
                    .to(q2("#sideMenuContentsCont"), {
                        duration: 0.6,
                        x: "0vw",
                    }, 'close');
            }
        }
    }, [menuOpen]);

    return (
        <>
            <div ref={el} style={width < widthColPx ? sideMenuContainerMobile : sideMenuContainer} >

                <div id="sideMenuCss" style={sideMenuCSS} onMouseEnter={onMenuHover} onMouseLeave={onMenuLeave} onMouseDown={onMenuClicked} >
                    <img id="sideMenuImg" style={imgCss} src={menuImg} />
                </div>

            </div>
            <div ref={el2} >
                <div id="sideMenuContentsCont" style={width < widthColPx ? sideMenuContentsContMobile : sideMenuContentsCont} >
                    <div style={sideMenuContents}>
                        <Link className="nav-link" to="MainPageView" spy={true} smooth={true} offset={50}
                            duration={1000} onClick={closeModal}>
                            <div style={menuItemsContainer} >
                                <img style={menuItemsImgCss} src={homeImg} />
                                <div style={menuItemsText}>
                                    Home
                                </div>
                            </div>

                        </Link>

                        <Link className="nav-link" to="ProjectPageView" spy={true} smooth={true} offset={50}
                            duration={1000} onClick={closeModal}>
                            <div style={menuItemsContainer} >
                                <img style={menuItemsImgCss} src={projectsImg} />
                                <div style={menuItemsText}>
                                    Projects
                                </div>
                            </div>

                        </Link>

                        <Link className="nav-link" to="whyJCPageView" spy={true} smooth={true} offset={50}
                            duration={1000} onClick={closeModal}>

                            <div style={menuItemsContainer} >
                                <img style={menuItemsImgCss} src={whyJCImg} />
                                <div style={menuItemsText}>
                                    Why J-Company
                                </div>
                            </div>

                        </Link>

                        <Link className="nav-link" to="ContactUsPageView" spy={true} smooth={true} offset={50}
                            duration={1000} onClick={closeModal}>

                            <div style={menuItemsContainer} >
                                <img style={menuItemsImgCss} src={companiesImg} />
                                <div style={menuItemsText}>
                                    ContactUs
                                </div>
                            </div>

                        </Link>

                        <br />

                    </div>

                </div>
            </div>

        </>
    )
}

export default SideMenuBar