import React from 'react';
import { useEffect } from 'react';
import { ReactComponent as DocIcon } from '../imgs/icons/019-doctor.svg';
import { ReactComponent as AlgoIcon } from '../imgs/icons/006-coding.svg';
import { ReactComponent as ConnectIcon } from '../imgs/icons/link.svg';
import { ReactComponent as AccessIcon } from '../imgs/icons/access.svg';
import mainVideo from '../videos/mainvideo.gif';
import { Parallax } from 'react-scroll-parallax';
import { Colors } from './Colors';
// imgs 
import androidImg from '../imgs/techicons/android.png';
import appleImg from '../imgs/techicons/apple.png';
import gpImg from '../imgs/techicons/google-play.png';
import linuxImg from '../imgs/techicons/linux-platform.png';
import winImg from '../imgs/techicons/windows.png';
import mainPage0102 from "../imgs/mpGifs/0102MainPage.gif";
import mainPage0304 from "../imgs/mpGifs/0304MainPage.gif";
// window size cal 
import useWindowDimensions from './getWindowsDimensions.js';

const MainPage = () => {
    const { height, width } = useWindowDimensions();
    const widthColPx = 1200 ; 
     
    useEffect(() =>
    {
        console.log("get Window Dimen : " + width, " w : h " + height)
    },[width]);

    const mainPageFlexMainContainer = {

        display: "flex",
        alignItems: "flex-start",
        justifyContent: "center",
        flexWrap: "wrap",
        height: "auto",
        width: "90%",
        maxWidth: "1400px",
        margin: "auto",
    };
    const mainPageFlexVertiContainer = {
        width: "48%",
        marginLeft: "2%",
        display: "flex",
        flexDirection: "column",
        alignItems: "flex-start",
        justifyContent: "center",
    };

    const mainPageFlexHoriContainer = {

        display: "flex",
        alignItems: "center",
        justifyContent: "center",

        minWidth: "300px",
        width: "50%",
        height: "100%"
    };

    const mainPageFlexVertiContainerWidthCh = {
        width: "100%",
        marginTop: "5vh",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "center",
    };

    const mainPageFlexHoriContainerWidthCh  = {

        display: "flex",
        alignItems: "center",
        justifyContent: "center",

        
        width: "100%",
        height: "100%"
    };

    const mainPageTitleCss = {
        fontSize: "3.5rem",
        fontWeight: "bold",
        color: Colors.medColor ,
        
    }
    const mainPageH1Css = {
        fontSize: "2rem",
        color: Colors.accentColor,
    }
    const techImgsContainer = {
        marginTop: "50px",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        width: "100%" , 
    }
    const techImgsItem = {
        maxWidth: "40px",
        marginRight: "10px",
    }

    function ImageMaskContainer({ marginTop, imgSrc, x, y }) {
        const imgMaskContainer = {
            position: "relative",
            overflow: "hidden",
            borderRadius: "50vw",
            
            height: "50vh",
            width: "50vw",
            maxWidth: "250px",
           
            marginTop: `${marginTop}` + "px",
        }
        // transform: 'translate(-50%, -50%)',
        return (
            <div style={imgMaskContainer}>
                <img src={imgSrc} style={{ margin:"auto", objectFit: "contain" ,width: "100%", height: "100%", position: "absolute", transform: 'translate(' + `${x}` + '%, ' + `${y}` + '%)' }} />
            </div>
        )
    }

    return (
        <div style={mainPageFlexMainContainer}>
            <div style={width > widthColPx ? mainPageFlexHoriContainer :  mainPageFlexHoriContainerWidthCh}>
                <Parallax speed={-10}>
                    <ImageMaskContainer marginTop="0" x="0" y="0" imgSrc={mainPage0102} />
                    
                </Parallax>
                <Parallax speed={10}>
                <ImageMaskContainer marginTop="0" x="0" y="0" imgSrc={mainPage0304} />
                </Parallax>
            </div>
            <div style={ width > widthColPx ?  mainPageFlexVertiContainer : mainPageFlexVertiContainerWidthCh}>
                <Parallax speed={5}>
                    <div style={mainPageTitleCss}>
                    
                      Connected anywhere
                     
                    </div>
                    <div style={mainPageTitleCss}>
                    Accessed any time
                       
                    </div>
                   
                    <br />
                    <div style={mainPageH1Css}>  Control anything
                    
                    </div>

                    <div> 
                    <br/>
                    <ConnectIcon style={{ width: "3rem", marginLeft: "10px", height: "auto", margin: "auto" }} />
                    <AccessIcon style={{ width: "3rem", marginLeft: "10px", height: "auto", margin: "auto" }} />
                        <br /> Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. 
                        
                    </div>
                </Parallax>
                <div style={techImgsContainer}>
                        <img style={techImgsItem} src={androidImg} />
                        <img style={techImgsItem} src={appleImg} />
                        <img style={techImgsItem} src={gpImg} />
                        <img style={techImgsItem} src={linuxImg} />
                        <img style={techImgsItem} src={winImg} />
                    </div>
            </div>

            

        </div>
    )
}

export default MainPage