import React from 'react';
import { useEffect, useState, useRef } from 'react';
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/all';
// test image 
import pressedBtn from "../imgs/testicon/1.png";
import highlightBtn from "../imgs/testicon/3.png";
import buttontest from "../imgs/testicon/2.png";
const BGColorChange = ({ scrHeight, colorTo, speId }) => {


    const [pressed, setPressed] = useState(false);
    const [hover, setHover] = useState(false);
    const bgcolorChangeView = {
        width: "100vw",
        height: `${scrHeight}` + "vh",

    }
    const fullBG = {
        width: "100vw",
        height: "100vh",
        position: "fixed",
        top: "0vh",
        zIndex: "-1",

    }
    const btnImg = {
        width: "130px",
        height: "67px"
    }
    function changeimg() {
        setPressed(!pressed);
    }
    function onHover() {
        setHover(true);
    }
    function onHoverLeave() {
        setHover(false);
    }
    const el = useRef();
    const q = gsap.utils.selector(el);
    useEffect(() => {
        const tl = gsap.timeline();

        /*
        gsap.registerPlugin(ScrollTrigger);
        //ScrollTrigger.refresh();
        //ScrollTrigger.normalizeScroll(true);
        ScrollTrigger.config({ 
            ignoreMobileResize: true
          });
          */
        // add box and circle anim to our timeline and play the first render 
        tl.current && tl.current.progress(0).kill();

        tl.current = gsap.timeline({
            scrollTrigger: {
                trigger: "#" + `${speId}`,
                start: "top center",   // trigger "left : trigger start position right: scroll postion "
                end: "bottom top ",
                invalidateOnRefresh : true, 
                    refreshPriority: 1,
                   
                scrub: 1,
                // pin: true causing pinning problem (moves after end ) 
            }
        }).to(q("#fullbgChange"), { //  + `${speId}`
            backgroundColor: "#" + `${colorTo}`,


        } )

            ;

    }, []);
    return (
        <>
            
            <div ref={el}>
                <div id={speId} style={bgcolorChangeView}> </div>
                <div id="fullbgChange" style={fullBG} > </div>
            </div>
        </>

    )
}

export default BGColorChange