import React from 'react';
import Slider from 'react-slick';
// needs npm install slick-carousel 

//import "slick-carousel/slick/slick-theme.css";
import "../Components/ComponentsCss/slick.css";

import { Parallax } from 'react-scroll-parallax';
import { useEffect, useState, useRef } from 'react';
import { Colors } from './Colors';
// company icons 
import fbIcon from "../imgs/compIcons/facebook.png";
import googleIcon from "../imgs/compIcons/google-plus.png";
import instaIcon from "../imgs/compIcons/instagram.png";
import linkenIcon from "../imgs/compIcons/linkedin.png";
import twitterIcon from "../imgs/compIcons/twitter.png";

import androidIcon from "../imgs/compIcons/005-android.png";
import appleIcon from "../imgs/compIcons/006-apple.png";
import cloudFIcon from "../imgs/compIcons/012-cloudflare.png";
import dockerIcon from "../imgs/compIcons/014-docker.png";
import intelIcon from "../imgs/compIcons/017-intel.png";
import samsungIcon from "../imgs/compIcons/025-samsung.png";
import raspIcon from "../imgs/compIcons/024-raspberry pi.png";
import ubuntuIcon from "../imgs/compIcons/032-ubuntu.png";

const CompanySlideShow = () => {
    const SlideShowCompContainer = {
        width: "100vw",
        paddingTop : "50px",
        paddingBottom : "50px",
        margin: "auto",

 
    }
    const SlideShowCompTextTitle = {
        color: Colors.projectDark4,

        marginBottom: "50px",
        marginLeft: "20px",
        
        fontSize: "1.5rem", 
        padding: "1rem",
        
    }
    const SlideShowCompImg = {
        height: "10vh",


        margin: "auto",
        objectFit: "contain"
    }

    const settings = {

        infinite: true,
        autoplay: true,
        autoplaySpeed: 0,
        speed: 3000,
        arrows: false,
        variableWidth: true,
        cssEase: "linear",
        dots: false,
        pauseOnHover: false
    };



    return (
        <div  >
            <div id="slideshowcont" style={SlideShowCompContainer}>
                <div style={SlideShowCompTextTitle}> Companies that work with us </div>
                <Slider id="slider" {...settings}>
                    <div>
                        <img style={SlideShowCompImg} src={androidIcon} className={"compImgSlick"} />
                    </div>
 
                    <div>
                        <img style={SlideShowCompImg} src={appleIcon} className={"compImgSlick"} />
                    </div>
                    <div>
                        <img style={SlideShowCompImg} src={cloudFIcon} className={"compImgSlick"} />
                    </div>
                    
                    <div>
                        <img style={SlideShowCompImg} src={dockerIcon} className={"compImgSlick"} />
                    </div>
                    <div>
                        <img style={SlideShowCompImg} src={intelIcon} className={"compImgSlick"} />
                    </div>
                    <div>
                        <img style={SlideShowCompImg} src={samsungIcon} className={"compImgSlick"} />
                    </div>
                    <div>
                        <img style={SlideShowCompImg} src={raspIcon} className={"compImgSlick"} />
                    </div>
                    <div>
                        <img style={SlideShowCompImg} src={ubuntuIcon} className={"compImgSlick"} />
                    </div>
                    
                    <div>
                        <img style={SlideShowCompImg} src={androidIcon} className={"compImgSlick"} />
                    </div>
 
                    <div>
                        <img style={SlideShowCompImg} src={appleIcon} className={"compImgSlick"} />
                    </div>
                    <div>
                        <img style={SlideShowCompImg} src={cloudFIcon} className={"compImgSlick"} />
                    </div>
                    
                    <div>
                        <img style={SlideShowCompImg} src={dockerIcon} className={"compImgSlick"} />
                    </div>
                    <div>
                        <img style={SlideShowCompImg} src={intelIcon} className={"compImgSlick"} />
                    </div>
                    <div>
                        <img style={SlideShowCompImg} src={samsungIcon} className={"compImgSlick"} />
                    </div>
                    <div>
                        <img style={SlideShowCompImg} src={raspIcon} className={"compImgSlick"} />
                    </div>
                    <div>
                        <img style={SlideShowCompImg} src={ubuntuIcon} className={"compImgSlick"} />
                    </div>
                </Slider>

            </div>
        </div>
    )
}

export default CompanySlideShow