import React from 'react';
import { useEffect, useState, useRef } from 'react';
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/all';

// window size cal 
import useWindowDimensions from './getWindowsDimensions.js';

const LogoScrollGSAP = ({ imgsrc }) => {

    const { height, width } = useWindowDimensions();
    const widthColPx = 800;

    const logo_img_container = {

        position: "fixed",
        top: "1vh",
        zIndex: "8",
        display: "block",
        marginLeft: "3%",
    }
    const comp_logo = {
        width: "100%",
        maxWidth: "100px",
        minWidth: "100px",
        transition: "0.3s",
        transformOrigin: "top left",
    }
    const comp_logo_mobile = {
        width: "100%",
        maxWidth: "50px",
        minWidth: "50px",
        transition: "0.3s",
        transformOrigin: "top left",
    }

    const el = useRef();
    const q = gsap.utils.selector(el);
    const tl = useRef();  // store the timeline in ref 
    useEffect(() => {

        gsap.registerPlugin(ScrollTrigger);
           /*
        gsap.registerPlugin(ScrollTrigger);
        //ScrollTrigger.refresh();
        //ScrollTrigger.normalizeScroll(true);
        ScrollTrigger.config({ 
            ignoreMobileResize: true
          });
          */
        //ScrollTrigger.normalizeScroll(true);
        // add box and circle anim to our timeline and play the first render 
        tl.current && tl.current.progress(0).kill();

        tl.current = gsap.timeline({
            scrollTrigger: {
                trigger: "#logogsap",
                start: "top top",
                end: "bottom top",

                scrub: 1,
                // pin: true causing pinning problem (moves after end ) 
            }
        })
            .fromTo(q("#logoimg"), {
                scale: 1,

            }, {
                scale: 0.5,

            })
            ;

    }, []);
    return (
        <div style={logo_img_container} ref={el} id="logogsap">
            <img style={width < widthColPx ? comp_logo_mobile : comp_logo} src={imgsrc} id="logoimg" />

        </div>

    )
}

export default LogoScrollGSAP